.promo__wrapper
  -webkit-box-orient: vertical
  -webkit-box-direction: normal
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -ms-flex-direction: column
  flex-direction: column
  gap: 10px 
  padding: 10px 10px 30px 10px

  &_desktop
    display: none

.promo__images-block
  -webkit-box-orient: vertical
  -webkit-box-direction: normal
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -ms-flex-direction: column
  flex-direction: column
  gap: 10px 

.promo__img
  height: calc(100vw - 10px)
  margin: 0 auto
  overflow: hidden
  width: 100%
  cursor: pointer

  img
    display: block
    height: 100%
    width: 100%

  &:hover img
    -webkit-transform: scale(1.1)
    -ms-transform: scale(1.1)
    transform: scale(1.1)

.promo__collage 
  display: flex
  justify-content: flex-start
  flex-wrap: wrap
  gap: 10px
  &-img
    width: calc(100% / 2 - 5px)
    height: calc(100vw / 2 - 15px)
    overflow: hidden
    cursor: pointer

    img 
      width: 100%
      height: 100%
      object-fit: cover

    &:hover
      img
        -webkit-transform: scale(1.1)
        -ms-transform: scale(1.1)
        transform: scale(1.1)

.offer
  -ms-flex-item-align: center
  -ms-grid-row-align: center
  align-self: center
  font-size: 16px
  line-height: 160%
  font-weight: 500
  text-align: center
  width: 100%
  padding: 0px

.promo__offer
  margin-bottom: 10px 
  padding: 0px 10px

.promo__bottom-offer
  margin: 10px 0px

.promo__text
  text-align: center
  display: flex
  flex-direction: column
  padding: 25px 0px

  span
    display: block
    margin-bottom: 30px
    font-weight: 700
    font-size: 24px
    line-height: 125%

.promo__advantages
  display: flex
  flex-direction: column
  align-items: center
  gap: 40px
  width: 100%
  height: max-content
  padding: 0px
  margin: 0px
  margin-bottom: 10px

.promo__advantage
  width: 100%
  list-style-type: none
  position: relative
  border-radius: 10px
  overflow: hidden
  transition: all .6s ease
  max-width: 300px

  span
    font-size: 16px
    line-height: 120%
    text-align: center
    margin-bottom: 0px
    font-weight: 500
  
  &:hover
    transform: scale(1.1)

@media (min-width: 576px)
  .promo__wrapper
    padding: 0px 0px 30px 0px

  .offer
    text-align: center
    font-size: 18px

  .promo__offer
    margin-bottom: 30px
    margin-top: 10px
    padding: 0px

  .promo__bottom-offer
    margin: 20px 0px

  .promo__collage 
    row-gap: 10px

  .promo__img
    height: calc(100vw - 20px)
    max-height: 560px
    width: 100%

  .promo__collage-img
    max-height: 275px

  .promo__text
    span
      font-size: 30px
      margin-bottom: 40px

  .promo__advantage
    max-width: 350px
    font-size:  20px
    span
      font-size: 20px
      margin-bottom: 0px

@media (min-width: 768px)
  .promo .offer
    font-size: 16px
    width: 100%

  .promo__collage
    row-gap: 1075px

  .promo__img
    max-height: 720px
    width: 100%

  .promo__collage-img
    max-height: 355px

@media (min-width: 992px)
  .promo__wrapper 
    flex-direction: row
    justify-content: flex-start
    flex-wrap: wrap
    gap: 20px

    &_desktop
      display: flex
    
    &_mobile
      display: none

  .promo .offer
    font-size: 18px
    width: 600px
    margin: 40px auto

  .promo .promo__bottom-offer
    margin: 0px auto 40px auto
    font-size: 20px
    width: 100%

  .promo__text
    width: 100%
    text-align: center

    span 
      margin-bottom: 75px

  .promo__advantages
    width: 100%
    flex-direction: row
    justify-content: center
    align-items: flex-start
    flex-wrap: wrap
    gap: 40px

  .promo__advantage
    width: calc(33% - 27px)
    height: 75px
    display: flex
    flex-direction: column
    justify-content: flex-start

  .promo__img
    width: 470px
    height: 470px
    margin: 0px
    &_last
      order: 1
      transform: none
  
  .promo__collage
    width: 470px
    height: 470px
    gap: 20px
    &-img
      width: 225px
      height: 225px

@media (min-width: 1200px)

  .promo__img
    width: 560px
    height: 560px

  .promo__collage
    width: 560px
    height: 560px
    &-img
      width: 270px
      height: 270px

@media (min-width: 1400px)
  .promo__wrapper
    gap: 40px

  .promo__img
    width: 660px
    height: 660px

  .promo__collage
    width: 660px
    height: 660px
    &-img
      width: 320px
      height: 320px