@keyframes backArrowMoving
	0%
		transform: translateX(0px)

	50%
		transform: translateX(-5px)

	100%
		transform: translateX(0px)

.project-gallery
	padding-top: 50px
	padding-bottom: 56px
	display: none
	opacity: 0
	transition: opacity 0.4s

	&_active
		display: block
		& .project-gallery__title, & .project-gallery__descr
			transform: translate(0px, 0px)
			

	&__back
		width: auto
		height: 40px
		position: absolute
		left: 20px
		top: 0px
		display: flex
		align-items: center
		gap: 10px
		cursor: pointer
		z-index: 100
		opacity: 0.5
		transition: .4s ease

		p	
			font-family: CeraPro, sans-serif
			font-size: 16px
			color: #333
			opacity: 0.8
			margin: 0
			padding: 0
			span
				display: none

		&:before 
			display: flex
			justify-content: center
			align-items: center
			width: 40px
			height: 40px
			background: #35353550
			border-radius: 100%
			font-size: 16px
			animation: backArrowMoving 1.2s ease infinite

		&:hover
			transform: scale(1.1)
			opacity: 1

	&__title
		width: 98%
		font-family: Wremena
		font-size: 24px	
		font-weight: 700
		text-align: right
		line-height: 125%
		color: #000
		margin-bottom: 10px
		transform: translate(-100vw, 15px)
		transition: transform .8s ease

	&__descr
		width: 98%
		font-size: 16px
		font-weight: 400
		text-align: right
		line-height: 125%
		color: #635046
		transform: translate(-100vw, 15px)
		transition: transform .4s .4s ease
		margin-bottom: 25px

	&__inner
		width: 100%
		display: flex
		flex-direction: column
		gap: 5px
		padding: 0px 5px 5px 5px

	&__imageRow
		display: flex
		width: 100%
		flex-direction: row
		flex-wrap: nowrap
		justify-content: space-between
		&:nth-child(odd)
			& .project-gallery__img-wrapper
				&:nth-child(1)
					width: calc(55% - 5px)
				&:nth-child(2)
					width: 45%

		&:nth-child(even)
			& .project-gallery__img-wrapper
				&:nth-child(1)
					width: 45%
				&:nth-child(2)
					width: calc(55% - 5px)

	&__img-wrapper 
		height: 40vw
		position: relative
		display: flex
		justify-content: center
		align-items: center
		overflow: hidden
		cursor: pointer
		img
			position: relative
			width: 100%
			height: 100%
			object-fit: cover
			transition: all 0s

		// &:nth-child(1)
		// 	box-shadow: 0px 5px 10px #000

		// &:nth-child(2)
		// 	box-shadow: 0px 5px 10px #000

		&:nth-last-child(1):first-child
			position: relative
			left: 50%
			width: calc(55% - 5px)
			transform: translateX(-50%)

@media screen and (min-width: 576px)
	.project-gallery
		.container
			width: 100% !important
			margin: 0 auto

		&__back
			height: 37.5px
			p
				font-size: 20px
				span
					display: inline

		&__title
			font-size: 30px
			margin-bottom: 20px

		&__descr
			font-size: 20px
			margin-bottom: 40px

		&__inner
			gap: 10px
			padding: 0px 10px 10px 10px

		&__imageRow
			gap: 10px
			&:nth-child(odd)
				& .project-gallery__img-wrapper
					&:nth-child(1)
						width: calc(55% - 10px)
					&:nth-child(2)
						width: 45%

			&:nth-child(even)
				& .project-gallery__img-wrapper
					&:nth-child(1)
						width: 45%
					&:nth-child(2)
						width: calc(55% - 10px)

		&__img-wrapper
			width: auto
			// height: 500px

			&:nth-last-child(1):first-child
				position: relative
				left: 50%
				width: calc(55% - 5px)
				transform: translateX(-50%)

@keyframes textAnimation
	0% 
		opacity: 0
		transform: scale(1.1)

	80%
		opacity: 1
		transform: scale(1.1)

	100%
		transform: scale(1)

@media screen and (min-width: 768px)
	.project-gallery
		padding-top: 55px
		&_active
			display: block
			& .project-gallery__title, & .project-gallery__descr
				opacity: 1
				animation: textAnimation .7s ease
		
		&__inner 
			width: 100% !important
			padding: 0px 15px 15px 15px
			gap: 15px

		&__imageRow
			flex-direction: row
			flex-wrap: nowrap
			justify-content: space-between
			gap: 15px
			&:nth-child(odd)
				& .project-gallery__img-wrapper
					&:nth-child(1)
						width: calc(55% - 15px)
					&:nth-child(2)
						width: 45%

			&:nth-child(even)
				& .project-gallery__img-wrapper
					&:nth-child(1)
						width: 45%
					&:nth-child(2)
						width: calc(55% - 15px)

		&__img-wrapper 
			height: 40vw

			&:nth-child(1)
				box-shadow: 0px 5px 10px #000

			&:nth-child(2)
				box-shadow: 0px 5px 10px #000

			&:nth-last-child(1):first-child
				position: relative
				left: 50%
				width: calc(55% - 15px)
				transform: translateX(-50%)

				// &:hover 
				// 	transform: translateX(-50%) scale(1.01)

		&__title
			text-align: center
			font-size: 36px
			opacity: 0
		
		&__descr
			text-align: center
			opacity: 0
			margin-bottom: 50px

		&__back
			height: 45px

@media screen and (min-width: 992px)
	.project-gallery
		padding-top: 72.5px
		&__title
			font-size: 50px

		&__descr
			font-size: 24px

		&__back
			height: 62.5px

@media screen and (min-width: 1400px)
	.project-gallery
		padding: 85px

		&__title
			font-size: 60px

		&__back
			height: 75px

