.header
  .logo__wrapper
    display: block
    height: 103px
    margin: 20px auto
    text-align: center
    text-decoration: none
    width: 108px

    .logo
      height: 100%
      width: 100%

  .menu
    background: rgba(0, 0, 0, 0.4)
    height: 100vh
    left: 0
    position: fixed
    top: 0
    -webkit-transform: translateX(-100vw)
    -ms-transform: translateX(-100vw)
    transform: translateX(-100vw)
    -webkit-transition: all 0.6s
    -o-transition: all 0.6s
    transition: all 0.6s
    width: 320px
    z-index: 3

  .menu_active
    -webkit-transform: translateX(0)
    -ms-transform: translateX(0)
    transform: translateX(0)

  .menu__list
    -webkit-box-orient: vertical
    -webkit-box-direction: normal
    -ms-flex-pack: distribute
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    -ms-flex-direction: column
    flex-direction: column
    font-size: 14px
    justify-content: space-around
    margin: 80px auto
    text-align: left
    width: 80%

  .menu__link
    color: #e1e1e1
    cursor: pointer
    display: block
    font-size: 24px
    font-weight: 500
    letter-spacing: 0.5px
    white-space: nowrap
    list-style-type: none
    text-decoration: none
    text-transform: uppercase
    -webkit-transition: all 0.3s
    -o-transition: all 0.3s
    transition: all 0.3s

    &:after
      background-color: #333
      content: ""
      display: block
      height: 2px
      -webkit-transform: translateY(-5px)
      -ms-transform: translateY(-5px)
      transform: translateY(-5px)
      -webkit-transition: all 0.3s
      -o-transition: all 0.3s
      transition: all 0.3s
      width: 0

  .menu__link_active
    color: #aaa
    cursor: default
    -webkit-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none

    &:after
      background-color: #a1a1a1
      content: ""
      height: 2px
      width: 75%

@media (min-width: 576px)
  .header .logo__wrapper
    height: 103px
    width: 108px

@media (min-width: 768px)
  .header
    .logo__wrapper
      height: 118px
      width: 124px

    .menu
      background-color: transparent
      height: 0
      left: 50%
      position: relative
      top: 0
      -webkit-transform: translateX(-50%)
      -ms-transform: translateX(-50%)
      transform: translateX(-50%)
      width: 80%

    .menu__list
      -webkit-box-orient: horizontal
      -webkit-box-direction: normal
      -ms-flex-pack: distribute
      display: -webkit-box
      display: -ms-flexbox
      display: flex
      -ms-flex-direction: row
      flex-direction: row
      justify-content: space-around
      margin: 0 auto
      text-align: center
      width: 100%

    .menu__link
      color: #333
      cursor: pointer
      display: block
      font-size: 13px
      font-weight: 500
      letter-spacing: 0.5px
      list-style-type: none
      text-decoration: none
      text-transform: uppercase
      -webkit-transition: all 0.3s
      -o-transition: all 0.3s
      transition: all 0.3s

      &:after
        background-color: #333
        content: ""
        display: block
        height: 0.5px
        margin: 0 auto
        -webkit-transform: translateY(-5px)
        -ms-transform: translateY(-5px)
        transform: translateY(-5px)
        -webkit-transition: all 0.3s
        -o-transition: all 0.3s
        transition: all 0.3s
        width: 0

      &:hover
        color: #919797

        &:after
          background-color: #919797
          content: ""
          height: 0.5px
          width: 97%

    .menu__link_active
      color: #919797
      cursor: default
      -webkit-user-select: none
      -moz-user-select: none
      -ms-user-select: none
      user-select: none

      &:after
        background-color: #919797
        content: ""
        height: 0.5px
        width: 97%

@media (min-width: 992px)
  .header
    .logo__wrapper
      height: 118px
      width: 124px

    .logo__wrapper__list
      font-size: 16px
      width: 70%

@media (min-width: 1200px)
  .header
    .logo__wrapper
      height: 147px
      width: 154px

    .menu__list
      margin: 0 auto
      width: 80%

.menu-btn
  -webkit-box-orient: vertical
  -webkit-box-direction: normal
  -webkit-box-pack: justify
  -ms-flex-pack: justify
  cursor: pointer
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -ms-flex-direction: column
  flex-direction: column
  height: 24px
  justify-content: space-between
  left: 30px
  position: absolute
  top: 30px
  -webkit-transition: all 0s
  -o-transition: all 0s
  transition: all 0s
  width: 24px
  z-index: 3

.menu-btn__item
  background-color: #333
  border-radius: 10px
  height: 4px
  -webkit-transition: all 0.3s
  -o-transition: all 0.3s
  transition: all 0.3s
  width: 100%

.menu-btn_active
  left: 30px
  position: fixed
  top: 30px
  width: 36px

  .menu-btn__item
    background-color: #e1e1e1

    &:first-child
      -webkit-transform: rotate(45deg) translateY(14px)
      -ms-transform: rotate(45deg) translateY(14px)
      transform: rotate(45deg) translateY(14px)

    &:nth-child(2)
      display: none

    &:last-child
      -webkit-transform: rotate(-45deg) translateY(-14px)
      -ms-transform: rotate(-45deg) translateY(-14px)
      transform: rotate(-45deg) translateY(-14px)

@media (min-width: 768px)
  .menu-btn
    display: none