.realization .container
    width: 100%
    padding: 0px 20px
    padding-bottom: 50px 
    max-width: 1550px
    
.realization__banner
    position: relative
    overflow: hidden
    width: 100%
    padding: 100px 30px

.realization__banner-image    
    position: absolute
    width: 100%
    height: 100%
    z-index: -1
    top: 0
    left: 0
    transition: none

    &::before
        content: ""
        width: 100%
        height: 100%
        position: absolute
        z-index: 1
        top: 0
        left: 0
        background: rgba(0, 0, 0, 0.7)


    img
        width: 100%
        height: calc(100% + 200px)
        position: absolute
        top: -200px
        left: 0
        z-index: 0
        object-fit: cover
        transition: none
        
.realization__title
    text-align: center
    font-size: 30px
    letter-spacing: 0.5px
    color: #fff
    max-width: 600px
    margin: 0 auto
    margin-bottom: 35px

.realization__subtitle
    font-size: 10px
    text-transform: uppercase
    text-align: center
    color: #fff
    max-width: 600px
    margin: 0 auto
    margin-bottom: 60px

.realization__banner-text
    color: #fff
    font-size: 14px
    text-align: center
    max-width: 600px
    margin: 0 auto
    margin-bottom: 20px
    display: flex
    justify-content: center
    flex-wrap: wrap
    gap: 5px 

    span
        white-space: nowrap


.realization__wrapper
    display: flex
    flex-direction: column
    padding-top: 15px
    gap: 15px
    max-width: 600px
    margin: 0 auto

.realization__image-block
    width: 100%
    height: max-content
    &_double
        display: flex
        flex-direction: column
        gap: 15px

        & .realization__image
            height: 325px

.realization__image
    width: 100%
    height: 665px
    cursor: pointer
    overflow: hidden

    &:hover
        img
            transform: scale(1.1)
    
    img
        width: 100%
        height: 100%
        object-fit: cover

.realization .bottom-buttons
    padding-bottom: 40px

@media (min-width: 768px)
    .realization .container
        padding: 0px 40px

    .realization__banner
        padding: 150px 40px

    .realization__banner-image
        
        img
            top: -180px
            max-height: 100vh

    .realization__title
        font-size: 68px
        margin-bottom: 50px

    .realization__subtitle
        font-size: 14px
        margin-bottom: 80px

    .realization__banner-text
        font-size: 22px
        margin-bottom: 33px

.realization .bottom-buttons
    padding-bottom: 50px

@media (min-width: 992px)
    .realization .container
        padding: 0px 75px
    
    .realization__banner
        padding: 150px 50px

    .realization__wrapper
        width: 100%
        max-width: 100%
        flex-direction: row
        flex-wrap: wrap
        gap: 20px

    .realization__image-block
        width: calc(50% - 10px)
        height: 660px

        &_double 
            gap: 20px
            & .realization__image 
                height: calc(50% - 10px)

    .realization__image
        width: 100%
        height: 100%
        


@media (min-width: 1200px)
    .realization .container
        padding: 0px 125px

    .realization__banner
        padding: 200px 75px

    .realization__banner-image
        
        img
            top: -210px

    .realization__banner-subtitle
        font-size: 15px
    
    .realization__banner-title
        font-size: 72px
    
    .realization__banner-text
        font-size: 24px

@media (min-width: 1400px)
    .realization .container
        padding: 0px 150px