.about__wrapper
  -webkit-box-pack: center
  -ms-flex-pack: center
  -webkit-box-orient: vertical
  -webkit-box-direction: normal
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -ms-flex-direction: column
  flex-direction: column
  justify-content: center
  padding-bottom: 50px 

.about__img
  margin-bottom: 30px
  width: 100%

  img
    height: 100%
    object-fit: fill
    width: 100%

.about__heading
  width: 96%
  margin: 0 auto
  margin-bottom: 30px

.about__lider
  width: 96%
  margin: 0 auto

.about__title 
  font-size: 28px
  font-weight: 300
  color: #444
  text-align: left !important
  width: 100%
  margin-bottom: 15px

.about__lider-introduction
  color: #444
  display: block
  font-size: 18px
  font-weight: 700
  line-height: 20px
  margin: 0px 0 20px

.about__text
  color: #777
  font-size: 14px
  font-weight: 500
  margin: 0 auto
  text-align: center
  width: 100%

.about__lider-text
  text-align: left

.about__lider-education 
  font-weight: 700
  font-size: 14px
  display: block
  margin: 10px 0px
  + ul
    width: 100%
    padding-left: 15px
    li 
      width: 100%
      margin-bottom: 10px
      list-style-type: none

@media (min-width: 576px) 
  .about__img 
    margin: 0px auto 25px auto


@media (min-width: 768px)
  .about__wrapper
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-box-align: center
    -ms-flex-align: center
    -webkit-box-orient: vertical
    -webkit-box-direction: normal
    align-items: center
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    -ms-flex-direction: column
    flex-direction: column
    justify-content: center

  .about__img
    position: relative
    width: 502px

  .about__heading
    width: 80%

  .about__lider
    width: 80%
    padding-bottom: 50px

  .about__lider-introduction
    width: 80%
    margin: 0 auto
    text-align: left
    margin: 0px auto 30px auto
    transform: translateX(-50px)

  .about__lider-text
    width: 100%

  .about__lider-education
    width: 100%
    + ul
      text-align: center
      &::after
        content: ''
        display: block
        width: 50px
        height: 2px
        background-color: #444
        margin: 30px auto
      
      li
        text-align: justify

  .about__title
    width: 80%
    margin: 0 auto
    text-align: left
    font-size: 38px
    margin: 0px auto 30px auto
    transform: translateX(-50px)

  .about__text
    color: #777
    font-size: 16px
    text-align: justify
    width: 87%

    span
      font-size: 18px
      margin: 30px 0 20px

@media (min-width: 992px)
  .about__wrapper
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-box-align: start
    -ms-flex-align: start
    -webkit-box-orient: horizontal
    -webkit-box-direction: normal
    align-items: flex-start
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    flex-wrap: wrap
    -ms-flex-direction: row
    flex-direction: row
    justify-content: center
    position: relative

  .about__title
    text-align: center !important
    transform: none

  .about__heading
    width: 100%
    margin-bottom: 50px

  .about__lider
    width: 50%

  .about__lider-introduction
    text-align: left
    width: 100%
    transform: none

  .about__lider-education
    font-size: 16px !important
    text-align: center
    width: 100%
    + ul
      width: 75%
      padding-left: 0
      margin-right: auto 
      margin-left: auto
      li
        font-size: 14px
        text-align: justify
        position: relative
        &::before 
          content: '-'
          font-size: 14px
          position: absolute
          top: 0px
          left: -10px


  .about__img
    margin-right: 25px
    width: 455px

  .about__text
    width: 100%

    &.about__lider-innerText:last-child
      display: block
      width: 455px
      transform: translate(calc(-100% - 25px), -205px)

  .about__lider-text
    text-align: left
  
  .about__heading-text
    text-align: center
    width: 80%

@media (min-width: 1200px)
  .about__img
    width: 545px

  .about__lider-education
    font-size: 19px !important
    + ul
      li
        font-size: 17px !important
        &::before
          font-size: 17px
          left: -15px

  .about__text
    &.about__lider-innerText:last-child
      width: 545px
      transform: translate(calc(-100% - 25px), -240px)

@media (min-width: 1400px)
  .about__img
    width: 600px
    margin-right: 40px

  .about__lider
    margin: 0px
    padding: 0px

  .about__text
    &.about__lider-innerText:last-child
      width: 160%
      transform: translateX(-50%)
      text-align: center
      font-size: 16px



  