.contact
  .contacts
    color: #333
    font-size: 22px
    font-weight: 400
    overflow: hidden
    text-align: center
    text-transform: uppercase
    width: 100%

  .contacts_deployed
    height: 150px

  .contacts__text
    margin-bottom: 20px
    text-align: center
    width: 100%

  .contacts__item
    color: #333
    display: block
    margin-bottom: 10px
    text-align: center
    -webkit-user-select: text
    -moz-user-select: text
    -ms-user-select: text
    user-select: text

  .contacts__socials
    -webkit-box-orient: vertical
    -webkit-box-direction: normal
    -webkit-box-pack: center
    -ms-flex-pack: center
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    -ms-flex-direction: column
    flex-direction: column
    justify-content: center

  .contacts__socials__title
    font-size: 26px
    font-weight: 500
    letter-spacing: 1px
    margin-bottom: 20px
    text-align: center
    text-transform: uppercase

  .contacts__socials-link
    color: #333
    display: block
    margin: 0 40px

  .contacts__icon
    display: block
    height: 128px
    margin: 0 auto 10px
    width: 128px

  .contacts__socials-text
    font-size: 20px
    font-weight: 500
    text-align: center
    text-transform: uppercase

@media (min-width: 576px)
  .contact .contacts__socials
    -webkit-box-orient: horizontal
    -webkit-box-direction: normal
    -ms-flex-direction: row
    flex-direction: row