[class*=" icon-"], [class^="icon-"]
  speak: never
  font-family: icofont !important
  font-style: normal
  font-variant: normal
  font-weight: 400
  line-height: 1
  text-transform: none

.icon-next:after
  content: "\e901"

.icon-prev:before
  content: "\e900"