.popup
  background: #e1e1e1
  display: none
  height: 100vh
  left: 0
  position: fixed
  top: 0
  opacity: 0
  width: 100vw
  z-index: 4

.popup_active
  display: block

.popup__deactivator
  background: transparent
  height: 100vh
  left: 0
  position: fixed
  top: 0
  width: 100vw
  z-index: 4

.popup__close-btn
  cursor: pointer
  height: 17px
  position: fixed
  right: 20px
  top: 20px
  width: 30px
  z-index: 1000

  &:hover
    -webkit-transform: scale(1.2)
    -ms-transform: scale(1.2)
    transform: scale(1.2)

.popup__close-btn-inner
  -webkit-box-orient: vertical
  -webkit-box-direction: normal
  -webkit-box-pack: justify
  -ms-flex-pack: justify
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -ms-flex-direction: column
  flex-direction: column
  height: 15px
  justify-content: space-between
  width: 30px

.popup__close-btn-item
  background-color: #333
  border-radius: 100px
  height: 3px
  width: 30px

  &:first-child
    -webkit-transform: rotate(-45deg) translateY(8.5px)
    -ms-transform: rotate(-45deg) translateY(8.5px)
    transform: rotate(-45deg) translateY(8.5px)

  &:nth-child(2)
    -webkit-transform: rotate(45deg) translateY(-8.5px)
    -ms-transform: rotate(45deg) translateY(-8.5px)
    transform: rotate(45deg) translateY(-8.5px)

.popup__wrapper
  background: transparent
  display: none
  height: 320px
  left: 50%
  opacity: 0
  overflow: hidden
  position: fixed
  top: 50%
  -webkit-transform: translate(-50%, -50%)
  -ms-transform: translate(-50%, -50%)
  transform: translate(-50%, -50%)
  -webkit-transition: 0.4s
  -o-transition: 0.4s
  transition: 0.4s
  width: 100%
  z-index: 4

.popup__wrapper_active
  -webkit-box-orient: vertical
  -webkit-box-direction: normal
  -webkit-box-pack: justify
  -ms-flex-pack: justify
  -webkit-box-align: start
  -ms-flex-align: start
  align-items: flex-start
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -ms-flex-direction: column
  flex-direction: column
  justify-content: space-between

.popup__track
  justify-content: space-between
  transition: 0.6s
  z-index: 4

.popup__item, .popup__track
  -webkit-box-align: center
  -ms-flex-align: center
  align-items: center
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  height: 100%
  padding: 0

.popup__item
  -webkit-box-pack: center
  -ms-flex-pack: center
  justify-content: center
  position: relative
  width: 100vw

.popup__img
  position: absolute
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  border: 0
  display: block
  height: 100%
  width: 100%
  object-fit: contain

.popup__buttons
  -webkit-box-pack: justify
  -ms-flex-pack: justify
  display: none
  height: 85px
  justify-content: space-between
  left: 50%
  opacity: 0
  position: fixed
  top: 0
  -webkit-transform: translate(-50%)
  -ms-transform: translate(-50%)
  transform: translate(-50%)
  -webkit-transition: 0.4s
  -o-transition: 0.4s
  transition: 0.4s
  width: 100vw
  z-index: 4

.popup__btn, .popup__buttons_active
  display: flex

.popup__btn
  color: #333
  cursor: pointer
  position: relative
  -webkit-transition: 0.4s
  -o-transition: 0.4s
  transition: 0.4s
  z-index: 4

  &::before
    display: flex
    justify-content: center
    align-items: center
    background: rgba(225, 225, 225, 0.7)
    width: 40px
    height: 40px
    border-radius: 100%

  &::after
    display: flex
    justify-content: center
    align-items: center
    background: rgba(225, 225, 225, 0.7)
    width: 40px
    height: 40px
    border-radius: 100%

.popup__btn_active
  display: flex

  &:before
    left: 10px
    opacity: 1
    position: fixed
    top: 50vh
    -webkit-transform: translateY(-50%)
    -ms-transform: translateY(-50%)
    transform: translateY(-50%)
    -webkit-transition: 0.4s
    -o-transition: 0.4s
    transition: 0.4s

  &:after
    opacity: 1
    position: fixed
    right: 10px
    top: 50vh
    -webkit-transform: translateY(-50%)
    -ms-transform: translateY(-50%)
    transform: translateY(-50%)
    -webkit-transition: 0.4s
    -o-transition: 0.4s
    transition: 0.4s

  &:hover
    &:before, &:after
      opacity: 1

@media screen and (min-width: 450px)
  .popup__wrapper
    height: 420px

@media (min-width: 576px)
  .popup__wrapper
    height: 530px

@media screen and (min-width: 700px)
  .popup__wrapper
    height: 600px

@media (min-width: 768px)
  .popup__wrapper
    background: transparent !important
    height: 100vh
    top: 0
    -webkit-transform: translate(-50%)
    -ms-transform: translate(-50%)
    transform: translate(-50%)
    width: 650px

  .popup__item
    width: 650px

  .popup__buttons_active
    display: -webkit-box
    display: -ms-flexbox
    display: flex

  .popup__btn
    font-size: 36px
    height: 100vh
    width: 50px

    &::after, &::before
      opacity: 1

  .popup__btn_active
    display: block

@media (min-width: 992px)
  .popup__wrapper
    height: 100vh
    top: 0
    width: 750px

  .popup__item
    margin: 0 auto
    width: 750px

  .popup__img
    display: block
    width: 100%
    object-fit: contain

  .popup__btn
    width: 75px

  .popup__btn_active
    &:before
      left: 20px

    &:after
      right: 20px

@media (min-width: 1200px)
  .popup__wrapper
    height: 100vh
    top: 0
    width: 950px

  .popup__item
    width: 950px

@media (min-width: 1400px)
  .popup__wrapper
    height: 100vh
    top: 0
    width: 1150px

  .popup__item
    width: 1150px

  .popup__btn
    width: 100px