.work__wrapper
  display: flex
  flex-flow: row wrap
  width: 100%
  justify-content: space-between
  align-items: center
  gap: 10px

.work
  .container 
    max-width: 1740px
    margin: 0 auto
    width: 100%

  .project
    margin-bottom: 14px
    overflow: hidden
    position: relative
    width: 100%
    height: auto
    background-color: #e1e1e1

  .project__inner
    padding: 10px
    display: flex
    flex-direction: column
    align-items: flex-start
    width: 100%
    height: auto
    justify-content: space-between
    transition: 0.2s
    width: 100%
    gap: 20px

  .project__title
    color: #33333390
    font-size: 15px
    font-weight: 500
    line-height: 120%
    opacity: 1
    text-align: left
    z-index: 6
    display: flex
    justify-content: flex-start
    align-items: flex-start
    width: 100%
    margin-bottom: 0px

    span
      span  
        display: block
        margin: 0 auto
        margin-bottom: 5px
        font-family: Wremena
        font-size: 30px
        color: #333333
        font-weight: 700
        line-height: 120%
        margin-bottom: 10px

  .project__img
    display: block
    width: 100%
    z-index: 0
    user-select: none
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.8)

  .project__btn
    width: 100%
    padding: 15px 0px
    border: 2px solid #333
    display: flex
    justify-content: center
    align-items: center
    text-align: center
    font-size: 15px
    line-height: 120%
    color: #333
    cursor: pointer
    background: transparent

@media (min-width: 576px)
  .work
    .project
      width: 50%

    .work__wrapper
      flex-flow: row wrap
      justify-content: space-between
      padding: 0px 10px
      gap: 0

    .project__img
      min-height: 250px
      height: 45vw
      max-height: 500px

@media (min-width: 768px)
  .work__wrapper
    flex-flow: row wrap
    justify-content: space-between

  .work
    .project
      width: 50%

    .project__title
      font-size: 18px

      span
        span
          font-size: 36px

@media (min-width: 992px)
  .work  
    
    .project__btn
      color: #66666690
      border: 2px solid #66666690
      font-weight: 400
      &:hover
        color: #333
        border: 2px solid #333

    .project__img
      max-height: 600px
      cursor: pointer
      &:hover
        transform: scale(0.98)
    
    .project__title
      font-size: 20px

      span
        span
          font-size: 40px

@media (min-width: 1200px)
  .work
    .work__wrapper
      padding: 0px 15px
      justify-content: center

    .project
      width: 33%

    .project__title
      font-size: 15px
      span
        span
          font-size: 30px

    .project__inner
      padding: 15px

    .project__img
      max-height: 510px
      height: 28vw



