.page-slider
    position: fixed
    top: 0
    left: 0
    z-index: 10
    display: none
    opacity: 0
    width: 100vw
    height: 100vh
    overflow: hidden
    background: #e1e1e1

.page-slider__track
    width: 100%
    height: auto
    min-height: 60vh
    position: absolute
    left: 0px
    top: 50%
    transform: translate(0, -50%)
    display: flex
    justify-content: flex-start
    align-items: center
    overflow: auto
    gap: 1000px
    width: max-content

    img
        display: block
        width: 100vw
        height: 100vw
        max-width: 768px
        max-height: 1200px
        object-fit: cover

.page-slider__btn


.page-slider__prevBtn
    position: absolute
    top: 50%
    left: 0px
    z-index: 11
    display: flex
    justify-content: flex-start
    align-items: center
    transform: translateY(-50%)
    width: 7.5vw
    height: 100vh
    padding-left: 20px
    font-size: 10px
    padding-right: 1px
    cursor: pointer

    &::before
        background: rgba(225, 225, 225, 0.7)
        border-radius: 100%
        width: 20px
        height: 20px
        text-align: center
        display: flex
        justify-content: center
        align-items: center
        flex-shrink: 0

.page-slider__nextBtn
    position: absolute
    top: 50%
    right: 0px
    z-index: 11
    display: flex
    justify-content: flex-end
    align-items: center
    font-size: 10px
    transform: translateY(-50%)
    width: 7.5vw
    height: 100vh
    padding-right: 20px
    cursor: pointer

    &::after
        background: rgba(225, 225, 225, 0.7)
        border-radius: 100%
        width: 20px
        height: 20px
        text-align: center
        display: flex
        justify-content: center
        align-items: center
        flex-shrink: 0

.page-slider__close
    position: absolute
    top: 20px
    right: 20px
    z-index: 12
    width: 30px
    height: 30px
    display: flex
    flex-wrap: wrap
    justify-content: center
    align-items: center
    cursor: pointer

    div
        width: 80%
        height: 2px
        background: #000
        &:nth-child(1)
            transform: rotate(45deg) translateY(10.5px)

        &:nth-child(2)
            transform: rotate(-45deg) translateY(-10.5px)
            

@media (min-width: 768px)
    .page-slider__track
        margin-left: calc((100vw - 768px) / 2)

    .page-slider__prevBtn
        font-size: 16px
        background: transparent
        &::before
            background: transparent

        &:hover
            background: transparent

    .page-slider__nextBtn
        font-size: 16px
        background: transparent
        &::after
            background: transparent

        &:hover
            background: transparent

    .page-slider__close
        width: 50px
        height: 50px
        
        div
            &:nth-child(1)
                transform: rotate(45deg) translateY(17px)

            &:nth-child(2)
                transform: rotate(-45deg) translateY(-17px)
