.services__banner
  display: none

.services .container 
  width: 100%
  margin: 0 auto
  padding: 0px 10px
  min-width: 300px
  
.services__stages-wrapper
  -webkit-box-pack: justify
  -ms-flex-pack: justify
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  flex-direction: column 
  justify-content: flex-start
  gap: 30px
  margin: 0
  padding: 0
  position: relative

.services__stages
  position: relative
  width: 100%
  padding-left: 40px

.services__stages-title
  font-size: 20px
  line-height: 120%
  font-weight: 400
  text-align: center
  text-transform: uppercase
  span
    font-weight: 700
    text-transform: none

.services__stages-description
  font-size: 12px
  line-height: 120%
  text-align: center
  width: 100%
  margin-bottom: 50px
  span
    display: none

.services__stages-line
  height: 100%
  width: 2px
  background-color: #ccc
  background: linear-gradient(#ccc 80%, transparent 85%)
  position: absolute
  left: 25px
  top: 20px

.services__stage
  text-align: left
  padding-left: 20px

.services__stage-img 
  display: none

.services__stage-title
  color: #666
  font-size: 18px
  font-weight: 300
  line-height: 135%
  text-align: left
  position: relative

  &::before 
    content: ''
    width: 40px
    height: 40px
    border-radius: 100%
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    top: 50%
    left: -54px
    transform: translateY(-50%)
    font-size: 12px
    font-weight: 500
    color: #444
    background: #b4b4b8

  &.--1
    &::before
      content: '1'

  &.--2
    &::before
      content: '2'

  &.--3
    &::before
      content: '3'

  &.--4
    &::before
      content: '4'

  &.--5
    &::before
      content: '5'

  &.--6
    &::before
      content: '6'

  &.--7
    &::before
      content: '7'

.services__stage-subtitle
  font-size: 18px
  font-weight: 500
  line-height: 135%
  margin-bottom: 18px
  text-align: left
  color: #333

.services__stage-list
  padding-left: 10px

.services__stage-item
  list-style-type: none
  margin-bottom: 10px

  p
    color: #333
    font-size: 13px
    font-weight: 400
    text-align: left

  &:last-child
    margin-bottom: 50px

.services__prices-wrapper
  width: 100%
  padding-top: 40px
  padding-bottom: 40px

.services__prices
  width: 100%
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  gap: 40px

.services__price
  width: 100%

  &:nth-child(2)
    order: 1

  &:last-child
    order: 2

  &:not(:last-child)
    &::after
      content: ''
      display: block
      width: 50px
      height: 2px
      background-color: #333
      margin: 20px auto 0px auto


.services__price-list
  width: 100%
  display: flex 
  flex-direction: column
  align-items: center
  gap: 15px
  padding: 0
  margin: 0

.services__prices-title
  font-size: 20px
  color: #111
  text-align: center
  font-weight: 600
  line-height: 31px
  margin-bottom: 30px
  span
    font-weight: 400

.services__price-title
  font-size: 14px
  font-weight: 500
  color: #111
  text-transform: uppercase
  text-align: center
  margin-bottom: 15px
  user-select: default !important
  span:not(.count)
    font-weight: 700
  
  .count
    display: block

.services__price-listItem
  font-size: 11px
  color: #111
  font-weight: 500
  text-transform: uppercase
  text-align: center
  list-style-type: none
  user-select: default !important

@media (min-width: 576px)
  .services .container
    min-width: 560px
    width: auto
    margin: 0px auto
    padding: 0px 20px

  .services__stages-description
    font-size: 14px

  .services__stages-line 
    left: 50%
    transform: translateX(-50%)

  .services__stages
    display: flex
    flex-direction: column
    row-gap: 20px
    padding: 0
    margin: 0

  .services__stage
    min-width: 220px
    width: calc(50% - 50px)
    padding-left: 0px
    &:nth-child(odd)
      align-self: flex-start
    
      & .services__stage-title, .services__stage-subtitle, .services__stage-item p
        text-align: right !important

      & .services__stage-title
        &::before
          left: calc(100% + 20px)

    &:nth-child(even)
      align-self: flex-end

      & .services__stage-title
        &::before
          left: -80px
      

  .services__stage-title
    color: #666
    font-size: 18px
    font-weight: 300
    line-height: 135%
    width: 100%
    text-align: left
    position: relative

    &::before 
      content: ''
      width: 60px
      height: 60px
      top: 50%
      left: -54px
      font-size: 14px

  .services__stage-title
    font-size: 20px

  .services__stage-subtitle
    font-size: 24px

  .services__stage-list
    padding-left: 0px

  .services__stage-item p
    margin-bottom: 15px
    font-size: 14px
    width: 100%

  .services__prices-wrapper
    width: 100%
    padding-top: 50px
    padding-bottom: 50px
  
  .services__prices
    width: 100%
    display: flex
    flex-direction: row
    justify-content: center
    align-items: flex-start
    flex-wrap: wrap
    gap: 25px

  .services__price
    position: relative  
    width: calc(33% - 17px)
    order: 0 !important

    &:nth-child(1), &:nth-child(2)
      &::after
        content: ''
        display: block
        width: 1px
        height: 250px
        position: absolute
        right: -12px
        top: 27px
        background: linear-gradient(transparent 5%, #333 33%, #333 66%, transparent 95%)
        margin: 0px

    &:nth-child(3)
      &::after
        content: ''
        display: none !important

  .services__prices-title
    margin-bottom: 50px

@media (min-width: 768px)
  .services .container
    width: 100%
    padding: 0px 40px
    margin: 0 auto

  .services__stage-title
    font-size: 20px

  .services__stage-subtitle
    font-size: 24px

  .services__stage-item p
    font-size: 16px

  .services__prices-wrapper
    padding: 75px 0px

  .services__prices-title
    font-size: 24px

@media (min-width: 992px)
  .services .container
    width: 100%
    padding: 0px 50px
    padding-top: 50px

  .services__banner
    display: block
    width: 100%
    height: calc(100vh / 2 + 50px)
    overflow: hidden

    img 
      object-fit: cover
      width: 100%
      height: 100%

  .services__stages-wrapper
    -webkit-box-pack: justify
    -ms-flex-pack: justify
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    position: relative
    padding-top: 50px

  .services__stages
    width: 100%
    row-gap: 60px

  .services__stages-description
    font-size: 16px
    span
      display: inline

  .services__stage-title

    &::before
      content: '' !important
      width: 8px
      height: 8px
      border: 1px solid #111
      background: #eee
      top: calc(50% + 80px)
  
  .services__stage
    width: calc(50% - 65px)
    position: relative

    &:nth-child(odd)
      & .services__stage-title, .services__stage-subtitle, .services__stage-item p
        text-align: left !important

      & .services__stage-title::before
        left: calc(100% + 61px)

    &:nth-child(even)
      & .services__stage-title, .services__stage-subtitle, .services__stage-item p
        text-align: right !important

      & .services__stage-title::before
        left: -69px

  .services__stage-img 
    display: block
    width: 100%
    position: absolute
    top: 50%
    transform: translateY(-50%)

    img
      width: 100%
      height: 100%
      object-fit: fill

    &.--1
      height: 170px
      left: calc(100% + 130px)

    &.--2
      height: 330px
      left: calc(100% + 130px)

    &.--3
      height: 325px
      left: calc(-130px - 100% )

    &.--4
      height: 340px
      left: calc(100% + 130px)


  .services__stage-item p
    color: rgba(0, 0, 0, 0.6)
    font-size: 16px

  .services__prices
    gap: 50px

  .services__price
    width: calc(33% - 34px)
    &:nth-child(1), &:nth-child(2)
      &::after
        right: -25px

    &:last-child
      &::before
        left: -25px

      &::after
        right: -25px

@media (min-width: 1200px)
  .services .container
    width: 100%
    padding: 0px 75px
    padding-top: 100px
    margin: 0 auto

  .services__stages-line
    background: linear-gradient(#ccc 80%, transparent 90%)

  .services__stage 
    width: calc(50% - 100px)

    &:nth-child(odd)
      & .services__stage-title::before
        left: calc(100% + 96px)
          
    &:nth-child(even)
      & .services__stage-title::before
        left: -104px

  .services__stage-img
    &.--1
      height: 170px
      left: calc(100% + 200px)

    &.--2
      height: 330px
      left: calc(100% + 200px)

    &.--3
      height: 325px
      left: calc(-200px - 100% )

    &.--4
      height: 340px
      left: calc(100% + 200px)

  .services__stage-item p
    font-size: 16px

  .services__prices-wrapper
    padding: 100px 0px

  .services__prices-title
    font-size: 26px

  .services__price
    &:nth-child(1), &:nth-child(2)
      &::after
        content: ''
        width: 2px
        top: 29px

  .services__price-title
    font-size: 16px

  .services__price-listItem
    font-size: 12px

@media (min-width: 1400px)
  .services .container
    width: 100%
    max-width: 1550px
    margin: 0 auto
    padding: 0px 75px
    padding-top: 100px

  .services__stages-line
    background: linear-gradient(#ccc 80%, transparent 95%)

  .services__stages-title
    font-size: 23px
  
  .services__stages-subtitle
    font-size: 26px
  
  .services__stage-item p
    font-size: 18px